@mixin btn() {
  color: $clr5;
  background-color: transparent;
  border: 1px solid $clr5;
  border-radius: 2px;
  padding: 0.75rem 1rem;
  font-family: $font-mono;
  line-height: 1;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
  margin-left: 15px;
  &:hover {
    outline: none;
    transform: translate(-4px, -4px);
  }
}

// Type

@mixin type($type: bold) {
  // type options:
  // 1.bold
  // 2.boldCondensed
  // 3.condensed
  @if $type == bold {
    font-variation-settings: "wght" 430;
  } @else if $type == boldCondensed {
    font-variation-settings: "wght" 430, "wdth" 80;
    letter-spacing: em(6.72px);
  } @else if $type == condensed {
    font-variation-settings: "wdth" 80;
    letter-spacing: em(6.72px);
  } @else if $type == light {
    font-variation-settings: "wght" 200;
  }
}

@mixin title($clr: $clr1, $size: 20px) {
  font-family: "Inter", sans-serif;
  color: $clr;
  font-size: $size;
  font-weight: 800;
}

@mixin font-size-scale($original-size) {
  font-size: calc(1vw / 16px) * #{$original-size};
}
